<script>
  export default {
      name: "Contact",
    data: () => ({
        center: { lat: 45.508, lng: -73.587 },
        marker: '',
        currentPlace: null,
        valid: false,
        rules: {
            required: value => value !== null ? !!value || "Required." : false,
            phone: value => value !== null ? /^\d{10}$/.test(value) || "Invalid contact number" : false,
            email: value => {
                if(value){
                    const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                    return pattern.test(value) || "Invalid e-mail.";
                } else {
                    return false;
                }
                
            },
        },
        contactForm: {
            name: '',
            email: '',
            contactNumber: '',
            message: '',
        },
    }),
     created(){
      this.marker = { lat: this.center.lat, lng: this.center.lng };
  },
  methods: {
    validateDetails(){
        if(this.$refs.form.validate()){
            if(this.contactForm.email === ""){
                this.contactForm.email = "Nil"
            }
            this.contactForm.formType = "contact"
            this.clearForm();
        }
    },
    clearForm(){
        this.contactForm = {
            name: null,
            email: null,
            contactNumber: null,
            message: null,
        }
    },
  }
  }
</script>
